
@mixin button {
    position: relative;
    top: 0px;
    padding: 5px 10px;
    display: block;
    margin: auto;
    background-color: #2876f9;
    background-image: linear-gradient(270deg, #2876f9 0%, #6d17cb 74%);
    border: 0;
    border-radius: 2px;
    color: white;
    cursor: pointer;
    &:hover {
        background-color :#ff4e00;
        background-image :linear-gradient(315deg, #ff4e00 0%, #ec9f05 74%);
    }
    &:active {
        top: 3px;
    }
padding: 7px 20px;
margin-top: 10px;
}

.customPopup {
    visibility: hidden;
    opacity: 0;
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    background: rgba(0, 0, 0, 0.3);
    transition: all 0.3s ease-in;

    &.show {
        transition: all 0.3s ease-out;
        visibility: visible;
        opacity: 1;

        
        .popupContainer {
            top: 50%;
            transition: all 0.3s ease-out;
        }
    }

    .popupContainer {
        display: flex;
        flex-flow: column;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: -500%;
        left: 50%;
        transform: translate(-50%, -50%);
        background: white;
        padding: 40px;
        border-radius: 2px;
        max-width: 90%;
        max-height: 90vh;
        overflow: scroll;
        transition: all 0.3s ease-in;

        .exitBtn {
            position: absolute;
            right: 10px;
            top: 4px;
            cursor: pointer;

            i {
                font-size: 16px;
                color: gray;
            }
        }

        .popMessageBox {
            padding: 10px;
            width: 100%;

            .title {
                margin: 0px;
            }

            .dateSelction {
                display: flex;
                justify-content: space-around;
                padding: 10px 20px;
                align-items: flex-end;
                flex-flow: wrap;

                div.dateChild {
                    margin: 0px  10px;
                }

                label {
                    font-weight: bold;
                }

                .react-datepicker-wrapper {
                    margin-top: 5px;

                    input {
                    width: 140px;
                    border: 0;
                    box-shadow: 0px 0px 5px 1px #d0d0d0;
                    min-height: 20px;
                    border-radius: 4px;
                    padding: 3px 7px;
                }
            }
                .searchButton {
                    @include button;
                }
            }

            .messageContent {
                padding: 10px;
                text-align: center;
                font-size: 1.2rem;

                #noData {
                    display: none;
                }

                table#table {
                    margin: auto;
                    border-collapse: collapse;
                    
                    th, td {
                        padding: 5px 10px;
                        border: 1px solid;
                    }
                }
            }

            .popupOkBtn {
                @include button;
            }
        }
    }
}

@media only screen and (max-width:900px) {
    .customPopup  {
        .popupContainer {
            max-height: 54vh;
            .popMessageBox .dateSelction div.dateChild {
                margin: 10px;
            }
        }
    }
}