header , footer {
    padding: 7px 10px;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 99;
    background: linear-gradient(
45deg
, #001b9d, #6a44ff);
    text-align: center;
    font-size: 1.5rem;
    font-weight: bold;
    color: white;
    text-transform: uppercase;
}

footer {
    top: unset;
    bottom: 0px;
}