
.listChild {
    width: 250px;
    box-shadow: 0px 0px 10px 1px #b6b6b6;
    cursor: pointer;
    transition: all 0.3s;
    margin: 10px;

    &:hover {
        transform: scale(1.05);
    }

    .imageContainer {
        width: 100%;
        display: flex;
        img {
            width: 100%;
        }
    }
    .userInfo {
        text-align: center;
        p {
            margin:0px;
            font-size: 1.2rem;
            font-weight: bold;
            padding: 5px 0px 8px;
            color: darkblue;
        }

        .userLoc {
            color: #148ff7;
        }
    }
}